.pr-progress {
  width: 3.5em;
  margin: 0 0.5em;
  padding-top: 4px; }
  .pr-progress.green .CircularProgressbar-path {
    stroke: url(#green-grad); }
  .pr-progress.red .CircularProgressbar-path {
    stroke: url(#red-grad); }
  .pr-progress .CircularProgressbar-path {
    stroke-linecap: round;
    transition: stroke-dashoffset 0.5s ease 0s; }
  .pr-progress .CircularProgressbar-trail {
    stroke: url(#grey-grad); }
  .pr-progress .CircularProgressbar-text {
    fill: #000000;
    font-size: 4em;
    font-weight: 300;
    dominant-baseline: middle;
    text-anchor: middle; }
  .pr-progress .CircularProgressbar-background {
    fill: #d6d6d6; }

@media screen and (max-width: 600px) {
  .pr-progress {
    width: 2.5em; }
  .CircularProgressbar-text {
    font-size: 3em; } }



